import React from 'react'
import { Title, Wrapper } from '../components/Heading';
import { LinkTag } from '../components/ContactStyles';

const ContactPage = () => (
  <Wrapper>
    <Title>contact me</Title>
    <p>If you are looking for a freelancer please email me at    
      <LinkTag href="mailto:cayetanomarvin@gmail.com">cayetanomarvin@gmail.com</LinkTag>
    </p>
  </Wrapper>
)

export default ContactPage


